/* card */
.card1 {
  width: 220px;
  height: 130px;
}
.card {
  position: relative;

  margin: 10px;
  overflow: hidden;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
    0 3px 1px -2px rgba(0, 0, 0, 0.2);
  transition: all 0.2s linear;
}

img {
  height: 100%;
  width: 100%;
  transition: all 0.2s linear;
}

.card-content {
  position: absolute;
  bottom: 3rem;
  right: 100%;
  width: 100%;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.7);
  transition: all 0.2s linear;
}

a {
  text-decoration: none;
}

.card-category {
  font-size: 22px;
  line-height: 20px;
  letter-spacing: 2px;
  margin: 15px;
  color: #000;
}

i {
  margin-left: 5px;
}

